/*Top Section Design: Carousel*/
#slider{
  z-index:-5;
}
.owl-item {
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1.0, 1.0);
}
#slider img{
  display: block;
  width: 100%;
  height: auto;
}
#slider .owl-item:before{
  content:"";
  position:absolute;
  top:0px;
  left:0px;
  width:100%;
  height:100%;
  background-color:rgba(0,0,0,0.35);
}
.owl-dots{
